/*
KISSY CSS Reset
理念：1. reset 的目的不是清除浏览器的默认样式，这仅是部分工作。清除和重置是紧密不可分的。
2. reset 的目的不是让默认样式在所有浏览器下一致，而是减少默认样式有可能带来的问题。
3. reset 期望提供一套普适通用的基础样式。但没有银弹，推荐根据具体需求，裁剪和修改后再使用。
特色：1. 适应中文；2. 基于最新主流浏览器。
维护：玉伯<lifesinger@gmail.com>, 正淳<ragecarrier@gmail.com>
*/
/** 清除内外边距 **/
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote,
dl, dt, dd, ul, ol, li,
pre,
form, fieldset, legend, button, input, textarea,
th, td {
  margin: 0;
  padding: 0;
}
/** 设置默认字体 **/
body,
button, input, select, textarea {
  font: 12px/1.5 'Source Sans Pro,PingFang SC,Helvetica Neue,Helvetica,Arial,sans-serif';
}
/* 解决 Mac 系统下字体发虚的问题 by mingcheng 2011-06-22 */
@media screen and (-webkit-min-device-pixel-ratio: 0) {
html body,
  button, input, select, textarea {
    font-family: 'Source Sans Pro','PingFang SC',Helvetica,Arial,sans-serif,"Helvetica Neue",Helvetica,"Hiragino Sans GB","Microsoft YaHei","\5FAE\8F6F\96C5\9ED1";
}
  /*
    input[type="text"], input[type="email"], input[type="search"], input[type="password"] {
    -webkit-appearance: caret;
}
*/
}
@-moz-document url-prefix() {
html body,
  button, input, select, textarea {
    font-family: 'Helvetica Neue', Helvetica, Tahoma, Arial, sans-serif;
}
}
/* h1, h2, h3, h4, h5, h6 { font-size: 100%; }*/
address, cite, dfn, em, var {
  font-style: normal;
}
/* 将斜体扶正 */
code, kbd, pre, samp {
  font-family: courier new, courier, monospace;
}
/* 统一等宽字体 */
small {
  font-size: 12px;
}
/* 小于 12px 的中文很难阅读，让 small 正常化 */
/** 重置列表元素 **/
ul, ol {
  list-style: none;
}
/** 重置文本格式元素 **/
a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
/** 重置表单元素 **/
legend {
  color: #000;
}
/* for ie6 */
fieldset, img {
  border: 0;
}
/* img 搭车：让链接里的 img 无边框 */
button, input, select, textarea {
  font-size: 100%;
}
/* 使得表单元素在 ie 下能继承字体大小 */
/* 注：optgroup 无法扶正 */
/** 重置表格元素 **/
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/** 移除 ios input shadow **/
input[type=text] {
  -webkit-appearance: none;
}
.app {
  text-align: center;
  padding: 50px 0 20px;
}
.example {
  border-bottom: 1px dashed #efefef;
  width: 400px;
  margin: 0 auto 40px;
  max-width: 100%;
}
.select {
  width: 350px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin: 30px auto 0;
  display: block;
  font-size: 14px;
  cursor: pointer;
}
h1 {
  font-size: 45px;
  color: #42b983;
}
h3 {
  margin-top: 20px;
  margin-bottom: 10px;
  color: #999;
  font-weight: normal;
  font-size: 18px;
  letter-spacing: 1px;
}
h3 a {
    color: #42b983;
}
h3 a:hover {
      text-decoration: none;
}

